* {
    box-sizing: border-box;
}

.buttons {  
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}

.buttons > .playButtons {  
    display: flex;

    position: relative;
    padding: 15px;
}
 
.buttons > .playButtons > button:nth-child(1) {
    animation-delay: 1s;
}

.buttons > .playButtons > button:nth-child(2) { 
    animation-delay: 0.5s;
}

.buttons > .playButtons > button { 
    animation: slide-top 0.5s forwards;
    align-items: center;
    background-color: lightblue; 
    border: 0;
    box-shadow: 0 0 2px black;
    color: white;
    display: flex;
    font-size: 16px;
    font-weight: bold; 
    height: 30px;
    justify-content: center;
    margin: 0 5px;
    outline: 0;
    text-shadow: 0 0 2px black; 
    transform: translateY(-60px);
    user-select: none;
    width: 30px;
}

.buttons > .controls > button{  
    border-radius: 5px;
    /* box-shadow: 0 0 2px black; */
    font-size: 50px;
    
}

.buttons > .playButtons > button:active {
    background-color: #f00a;
}

.buttons > .controls {
    bottom: 0;  
    height: 120px;
    position: absolute;
    width: 100vw;
}
  
.buttons > .controls > * {  
    border: 0; 
    color: #fff;
    outline: 0;
    text-shadow:  0 0 2px black;
    transition: all 0.2s;
}

.buttons > .controls > button:nth-child(1) {
    background-color: #add8e6aa;
}

.buttons > .controls > button:nth-child(2) {
    background-color: #90ee90aa;
}

.buttons > .controls > button:active { 
    transform: scale(0.95);
    background-color: #f003;    
}

@media only screen and (max-width: 2000px) {
    .buttons > .controls {
        display: none; 
    }
}

@media only screen and (max-width: 1000px) {
    .buttons > .controls {
        display: flex;
        justify-content: space-between;
        padding: 10px;
    }
    .buttons > .controls > button {
        width: 200px;
    }
}

@media only screen and (max-width: 500px) {
    .buttons > .controls { 
        display: grid;
        grid-gap: 10px;
        grid-template-columns: repeat(2, 1fr);  
        padding: 10px;
    }
    .buttons > .controls > button { 
        width: auto;
    }
}

@keyframes slide-top {
    0% {
        transform: translateY(-60px);
    }
    50% {
       transform: translateY(10px);
    }
    100% {
        transform: translateY(0px);
    }
}

